import "../Layout.css";
import Footer from "./Footer";
import Header from "./Header";
import { Container } from "@mui/material";
import * as React from "react";

const images = [
	"arab_tile",
	"bush",
	"circuit",
	"full-bloom",
	"gplaypattern",
	"greek-vase",
	"herringbone",
	"hotel-wallpaper",
	"more-leaves",
	"moroccan-flower",
	"moroccan-flower-dark",
	"morocco-blue",
	"new_year_background",
	"regal",
	"ripples",
	"tree_bark",
	"trees",
];

const Layout = (props) => {
	const [mainClassName, setMainClassName] = React.useState("");

	const handlerBgTileChange = (bgTileIndex) => {
		setMainClassName(`main main_${images[bgTileIndex]}`);
	};

	// React.useEffect(() => {
	// 	setMainClassName(`main main_${images[bgTileIndex]}`);
	// }, [bgTileIndex]);

	return (
		<div className={mainClassName}>
			<Header onBgTileChange={handlerBgTileChange} images={images} />
			<Container className="container">
				{/*<SoapLabels/>*/}
				{/*<SoapOilsCalculator />*/}
				{props.children}
			</Container>
			<Footer />
		</div>
	);
};
export default Layout;
