import Layout from "../layout/Layout";
import LabelsList from "./LabelsList";
import LabelsPrintView from "./LabelsPrintView";
import "./SoapLabels.css";
import store from "./store/";
import { useSoapLabels, useSettings } from "./store/actions";
import { Box } from "@mui/material";
import * as React from "react";
import { Provider } from "react-redux";

const Main = (props) => {
	const soapLabels = useSoapLabels();
	const settings = useSettings();

	return (
		<Layout>
			<Box className="box containerPage">
				{settings && soapLabels && (
					<React.Fragment>
						<LabelsList />
						<LabelsPrintView />
					</React.Fragment>
				)}
			</Box>
		</Layout>
	);
};

const SoapLabels = (props) => {
	return (
		<Provider store={store}>
			<Main />
		</Provider>
	);
};

export default SoapLabels;
