// import App from "./App";
import "./SoapHelper/App.css";
import Home from "./SoapHelper/Home/Home";
import SoapLabels from "./SoapHelper/SoapLabels/SoapLabels";
import SoapOilsCalculator from "./SoapHelper/SoapOilsCalculator/SoapOilsCalculator";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import DateAdapter from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import CssBaseline from "@mui/material/CssBaseline";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import React from "react";
import ReactDOM from "react-dom";
import { Routes, Route, HashRouter } from "react-router-dom";

const theme = createTheme({
	typography: {
		fontFamily: [
			"Lato",
			"Arial",
			"Shadows Into Light",
			"-apple-system",
			"BlinkMacSystemFont",
			'"Segoe UI"',
			"Roboto",
			'"Helvetica Neue"',
			"Arial",
			"sans-serif",
			'"Apple Color Emoji"',
			'"Segoe UI Emoji"',
			'"Segoe UI Symbol"',
		].join(","),
		button: {
			fontSize: "1rem",
			fontWeight: "600",
			textTransform: "none",
		},
	},
});

ReactDOM.render(
	<LocalizationProvider dateAdapter={DateAdapter}>
		<ThemeProvider theme={theme}>
			<CssBaseline />
			<HashRouter>
				<Routes>
					<Route path="/" element={<Home />} />
					<Route path="/labels" element={<SoapLabels />} />
					<Route
						path="/calculator"
						element={<SoapOilsCalculator />}
					/>
				</Routes>
			</HashRouter>
		</ThemeProvider>
	</LocalizationProvider>,
	document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
